.logoReact {
  height: 100px;
  pointer-events: none;
}

.logo {
  height: 80px;
  pointer-events: none;
}

.link {
  color: white;
  text-decoration: none;
}

@media (prefers-reduced-motion: no-preference) {
  .logoReact {
    animation: logo-spin infinite 20s linear;
  }
}

@keyframes logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.skills {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
  max-width: 1080px;
  margin: auto;
  height: 100vh;
  justify-content: center;
}

.subSkills {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  width: 250px;
  height: 90%;
}

.dialogContent {
  width: 550px;
  height: 450px;
}

.card {
  margin: 20px;
  padding: 10px 20px;
  width: 100%;
  max-width: 1080px;
  display: flex;
  flex-direction: row;
  align-items: center;
}

.logoContainer {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 30%;
}

.listItem {
}

.containContainer {
  width: 100%;
}

.subTitle {
  color: white;
  font-size: 40px;
}

.chip {
  margin: 0 5px;
  &:hover {
    background-color: dimgray;
  }
}
